<template>
    <div class="check-validator">
        <el-dialog
                :visible.sync="visible"
                width="100%">
            <i class="el-icon-loading"/>{{message}}
        </el-dialog>
        <face-capture v-if="faceCaptureVisible" ref="faceCapture"/>
    </div>
</template>

<script>
    // 

    import {check} from "../../../../../resource/check";
    import {getCurrentPosition, wait} from "../../../../../utils";
    import FaceCapture from "./FaceCapture";

    export default {
        name: "CheckValidator",
        components: {FaceCapture},
        props: {
            groupId: {
                type: String,
                required: true
            },
        },
        created(){

        },
        data() {
            return {
                visible: false,
                faceCaptureVisible: false,
                message: ''
            }
        },
        methods: {
            async check(needLocation = false, needFace = false) {
                this.visible = true

                try {
                    this.message = ''

                    let point = null
                    let face = null


                    if (needLocation) {

                        // 
                        try {
                            this.message = ''
                            point = await getCurrentPosition()
                            this.message = ''
                            wait(1000)
                        } catch (e) {
                            if (e.info === 'NOT_SUPPORTED') {
                                this.$message.error('')
                            } else {
                                this.$message.error('' + e.message)
                            }
                            throw e
                        }
                    }

                    if (needFace) {

                        //
                        this.message = ''
                        this.faceCaptureVisible = true
                        this.visible = false
                        await new Promise(resolve => {
                            this.$nextTick(() => {
                                resolve()
                            })
                        })
                        face = await this.$refs.faceCapture.getNormalFrame()
                        this.visible = true
                        this.message = ''
                        this.faceCaptureVisible = false
                        await wait(1000)
                        this.visible = true
                    }


                    this.message = ''
                    let form = new FormData()

                    if (point) {
                        form.append('lng', point.lng)
                        form.append('lat', point.lat)
                    }
                    if (face) {
                        form.append('face', face)
                    }
                    await check(this.groupId, form)
                    return true
                } catch (e) {
                    console.log(e)
                    this.$message.error(e.message)
                    throw e
                } finally {
                    this.visible = false
                }

            },
        }
    }
</script>

<style scoped lang="scss">

</style>
<style lang="scss">
    .check-validator {
        .el-dialog__header {
            display: none !important;
        }
    }
</style>